* {
    padding: 0;
    margin: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
  }
  body {
    overflow:hidden;
  }
  .demo8 {
    width: 100%;
    overflow:scroll;
  }
  .demo8-item {
    position: absolute;
    width:100%;
    pointer-events: auto;
    transform-origin: 50% 50% 0px;
    border-radius: 0px;
    color: rgb(153, 255, 153);
    line-height: 48px;
    padding-left: 32px;
    font-size: 24px;
    font-weight: 400;
    background-color: rgb(200, 200, 255);
}
